import React, { useRef, useState, useEffect } from "react";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import Grid from "@mui/material/Grid";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import * as yup from "yup";
import { useFormik } from "formik";
import Autocomplete from "@mui/material/Autocomplete";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import TableHead from "@mui/material/TableHead";


const validationSchema = yup.object({
  name: yup
    .string()
    .required("Name is required")
    .min(3, "Name must be at least 3 characters"),
  age: yup
    .number()
    .positive("Age must be a positive number")
    .required("Age is required"),
  sex: yup
    .string()
    .required("Sex is required")
    .oneOf(["Male", "Female"], "Invalid value for Sex"),
  mobileNumber: yup
    .string()
    .required("Mobile Number is required")
    .matches(/^[6-9]\d{9}$/, "Invalid mobile number"),
  govtIdType: yup.string().required("Govt Issue ID Type is required"),
  govtId: yup.string().required("Govt ID is required"),
  


});

function RegistrationForm() {
  const myRef = useRef(null);

  const theme = createTheme(); // Create a theme
  const [formData, setFormData] = useState([]);
  const [showAddress, setShowAddress] = useState(false);
  const [countryOptions, setCountryOptions] = useState([]);


  useEffect(() => {
    const fetchCountryOptions = async () => {
      try {
        const response = await fetch("https://restcountries.com/v3.1/all");
        const countries = await response.json();
        setCountryOptions(countries.map((country) => country.name.common));
      } catch (error) {
        console.error("Error fetching country options", error);
      }
    };

    fetchCountryOptions();
  }, []);
  const formik = useFormik({
    initialValues: {
      name: "",
      age: "",
      sex: "",
      mobileNumber: "",
      govtIdType: "",
      govtId: "",
    },
    validationSchema: validationSchema,

     onSubmit: async (values) => {
      const combinedData = {
        ...values,
        address: formik.values.address,
        state: formik.values.state,
        city: formik.values.city,
        country: formik.values.country,
        pincode: formik.values.pincode,
      };

      setFormData((prevData) => [...prevData, combinedData]);
      try {
        console.log("Form submitted successfully", formData);
      } catch (error) {
        console.error("Error submitting form", error);
      }

      setShowAddress(true);
      
    },
  });


  return (
    <ThemeProvider theme={theme}>
      <div className="registration-form-container">
        <h1>Registration Form </h1>

        <form
          onSubmit={formik.handleSubmit}
          className={showAddress ? "hideForm" : ""}
        >
          <h3 className="details-header">Personal Details</h3>
          <Grid container spacing={1}>
            <Grid item xs={12} sm={4}>
              <TextField
                label="Name"
                variant="outlined"
                fullWidth
                margin="normal"
                name="name"
                value={formik.values.name}
                onChange={formik.handleChange}
                error={formik.touched.name && Boolean(formik.errors.name)}
                helperText={formik.touched.name && formik.errors.name}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                label="Age"
                type="number"
                variant="outlined"
                fullWidth
                margin="normal"
                name="age"
                value={formik.values.age}
                onChange={formik.handleChange}
                error={formik.touched.age && Boolean(formik.errors.age)}
                helperText={formik.touched.age && formik.errors.age}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <FormControl fullWidth margin="normal" className="fields-align">
                <InputLabel id="sex-label">Sex</InputLabel>
                <Select
                  labelId="sex-label"
                  label="Sex"
                  name="sex"
                  value={formik.values.sex}
                  onChange={formik.handleChange}
                >
                  <MenuItem value="Male">Male</MenuItem>
                  <MenuItem value="Female">Female</MenuItem>
                  <MenuItem value="other">Other</MenuItem>
                </Select>
                {formik.errors.sex && formik.touched.sex && (
                  <div className="formik-error-message">
                    {formik.errors.sex}
                  </div>
                )}
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={4}>
              <TextField
                label="Mobile Number"
                type="tel"
                variant="outlined"
                fullWidth
                margin="normal"
                name="mobileNumber"
                value={formik.values.mobileNumber}
                onChange={formik.handleChange}
                error={
                  formik.touched.mobileNumber &&
                  Boolean(formik.errors.mobileNumber)
                }
                helperText={
                  formik.touched.mobileNumber && formik.errors.mobileNumber
                }
              />
            </Grid>
            <Grid item xs={12} sm={4} lg={2}>
              <FormControl fullWidth margin="normal">
                <InputLabel id="id-type-label">Govt Issue ID Type</InputLabel>
                <Select
                  labelId="id-type-label"
                  label="Govt Issue ID Type"
                  name="govtIdType"
                  value={formik.values.govtIdType}
                  onChange={formik.handleChange}
                >
                  <MenuItem value="Adhar">Adhar</MenuItem>
                  <MenuItem value="PAN">PAN</MenuItem>
                </Select>
                {formik.errors.govtIdType && formik.touched.govtIdType && (
                  <div className="formik-error-message">
                    {formik.errors.govtIdType}
                  </div>
                )}
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={4} lg={2}>
              <TextField
                label="Govt ID"
                variant="outlined"
                fullWidth
                margin="normal"
                name="govtId"
                value={formik.values.govtId}
                onChange={formik.handleChange}
                error={formik.touched.govtId && Boolean(formik.errors.govtId)}
                helperText={formik.touched.govtId && formik.errors.govtId}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                className="mt-4"
                type="submit"
                variant="contained"
                color="primary"
              >
                Next
              </Button>
            </Grid>
          </Grid>
        </form>
        {showAddress && (
          <form onSubmit={formik.handleSubmit}>
            <h3 className="details-header">Address Details</h3>
            <Grid container spacing={1}>
              <Grid item xs={12} sm={6} lg={4}>
                <TextField
                  label="Address"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  name="address"
                  value={formik.values.address}
                  onChange={formik.handleChange}
                  error={
                    formik.touched.address && Boolean(formik.errors.address)
                  }
                  helperText={formik.touched.address && formik.errors.address}
                />
              </Grid>
              <Grid item xs={12} sm={6} lg={4}>
                <TextField
                  label="State"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  name="state"
                  value={formik.values.state}
                  onChange={formik.handleChange}
                  error={formik.touched.state && Boolean(formik.errors.state)}
                  helperText={formik.touched.state && formik.errors.state}
                />
              </Grid>
              <Grid item xs={12} sm={6} lg={4}>
                <TextField
                  label="City"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  name="city"
                  value={formik.values.city}
                  onChange={formik.handleChange}
                  error={formik.touched.city && Boolean(formik.errors.city)}
                  helperText={formik.touched.city && formik.errors.city}
                />
              </Grid>
              <Grid item xs={12} sm={6} lg={4}>
            <Autocomplete
              options={countryOptions}
              fullWidth
              value={formik.values.country}
              onChange={(event, newValue) => {
                formik.setFieldValue("country", newValue || "");
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Country"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  name="country"
                />
              )}
            />
          </Grid>
              <Grid item xs={12} sm={6} lg={4}>
                <TextField
                  label="Pincode"
                  variant="outlined"
                  fullWidth
                  type="number"
                  margin="normal"
                  name="pincode"
                  value={formik.values.pincode}
                  onChange={formik.handleChange}
                />
              </Grid>
            </Grid>
            <Button
              className="mt-4"
              variant="contained"
              type="submit"
              color="primary"
            >
              Submit
            </Button>
          </form>
         )}
          {formData.length > 0 && (
  <TableContainer component={Paper} className="mt-4">
    <Table>
    <TableHead>
      <TableRow>
        <TableCell>Name</TableCell>
        <TableCell>Age</TableCell>
        <TableCell>Sex</TableCell>
        <TableCell>Mobile Number</TableCell>
        <TableCell>Govt ID</TableCell>
        <TableCell>City</TableCell>
        <TableCell>Country</TableCell>
        <TableCell>Pincode</TableCell>
      </TableRow>
    </TableHead>
      <TableBody>
        {formData.map((data, index) => (
          <TableRow key={index}>
            <TableCell>Name</TableCell>
            <TableCell>{data.name}</TableCell>
            <TableCell>{data.age}</TableCell>
            <TableCell>{data.sex}</TableCell>
            <TableCell>{data.mobileNumber}</TableCell>
            <TableCell>{data.govtID}</TableCell>
            <TableCell>{data.city}</TableCell>
            <TableCell>{data.country}</TableCell>
            <TableCell>{data.pincode}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  </TableContainer>
)}

      </div>
    </ThemeProvider>
  );
}

export default RegistrationForm;

